




























import { Vue, Component, Prop } from 'vue-property-decorator';
import { editCategory, addCategory, getCategoryInfo, getCategoryList } from '@/api/category';

@Component
//  import moduleName from 'modulePath';
export default class CategoriesEdit extends Vue {
  public model: CategoriesModel = {
    name: '',
    parent: [],
  };
  public parents: object[] = [];
  @Prop({type: String}) public id!: string;
  public created() {
    this.fetchParents();
    this.id && this.fetch();
  }
  // id
  public async save() {
    if (this.model.parent === '') {
      delete this.model.parent;
    }
    let res;
    if (this.id) {
      res = await editCategory(this.id, this.model);
    } else {
      res = await addCategory(this.model);
    }
    this.$router.push('/categories/list');
    this.$message({
      type: 'success',
      message: '',
    });
  }
  public async fetch() {
    const res: any = await getCategoryInfo(this.id);
    this.model = res.data;
  }
  // 
  public async fetchParents() {
    const res: any = await getCategoryList('getParent');
    this.parents = res.data;
  }
}
