
















































import { Vue, Component, Prop } from 'vue-property-decorator';
import { editAds, addAds, getAdsInfo } from '@/api/ads';

@Component
//  import moduleName from 'modulePath';
export default class AdaEdit extends Vue {
  public model: AdsModel = {
    name: '',
    items: [],
  };
  @Prop({type: String}) public id!: string;
  public created() {
    this.id && this.fetch();
  }
  // id
  public async save() {
    let res;
    if (this.id) {
      res = await editAds(this.id, this.model);
    } else {
      res = await addAds(this.model);
    }
    this.$router.push('/ads/list');
    this.$message({
      type: 'success',
      message: '',
    });
  }
  public async fetch() {
    const res = await getAdsInfo(this.id);
    this.model = Object.assign({}, this.model, res.data);
  }
}
